<template>
  <b-navbar type="dark" variant="info" class="navbar">
    <router-link to="/" tag="b-navbar-brand">Vue Recipes</router-link>

    <b-navbar-nav>
      <router-link to="/recipes" tag="b-nav-item">Recipes </router-link>
    </b-navbar-nav>

    <!-- Right aligned nav items -->
    <b-navbar-nav v-if="!$root.loggedIn" class="ml-auto mr-2">
      <b-nav-item-dropdown text="Guest" right>
        <router-link to="/register" tag="b-dropdown-item">Register</router-link>
        <router-link to="/login" tag="b-dropdown-item">Login</router-link>
      </b-nav-item-dropdown>
    </b-navbar-nav>
    <b-navbar-nav v-else class="ml-auto mr-2">
      <b-nav-item-dropdown text="User" right>
        <b-dropdown-item href="#">Profile</b-dropdown-item>
        <b-dropdown-item @click="Logout">Logout</b-dropdown-item>
        <!-- <b-dropdown-item href="#">Sign Out</b-dropdown-item> -->
      </b-nav-item-dropdown>
    </b-navbar-nav>
  </b-navbar>
</template>

<script>
export default {
  methods: {
    Logout() {
      this.$root.loggedIn = false;
      this.$root.toast("Logout", "User logged out successfully", "success");

      this.$router.push("/").catch((err) => {
        this.$forceUpdate();
      });
    }
  }
};
</script>

<style>
.navbar {
  margin-bottom: 5vh;
}
</style>
